import { Card, Row, Col } from "react-bootstrap"

const Adresse = () => {
    return (
        <Row className="info"  >
            <h3> Vous avez une question, un projet ? Vous souhaitez recevoir un devis ?  </h3>
            <Col className="phone">
                <Card style={{ width: '18rem' }}>
                    <Card.Img variant="top" src="data:image/png;base64," style={{ width: '50px' }} />
                    <Card.Body>
                        <Card.Title>Téléphone</Card.Title>
                        <Card.Text>
                            +33 9 71 00 48 42
                        </Card.Text>
                    </Card.Body>
                </Card>

            </Col>
            <Col className="mail">
                <Card style={{ width: '18rem' }}>
                    <Card.Img variant="top" src="data:image/png;base64," style={{ width: '50px' }} />
                    <Card.Body>
                        <Card.Title>E-mail</Card.Title>
                        <Card.Text>
                            contact@plaster.lu
                        </Card.Text>
                    </Card.Body>
                </Card>
            </Col>

            <Col className="mail">
                <Card style={{ width: '18rem' }}>
                    <Card.Img variant="top" src="data:image/png;base64," style={{ width: '50px' }} />
                    <Card.Body>
                        <Card.Title>Adresse</Card.Title>
                        <Card.Text>
                            7 rue des Roches
                            <br />
                            57000 Metz
                            <br />
                            France
                        </Card.Text>
                    </Card.Body>
                </Card>

            </Col>

        </Row>
    )
}

export default Adresse

